<template>
  <div>
    <div class="page-title">{{ tag }}</div>
    <ul class="ul-list">
      <li class="tag-list" v-for="(item, index) of posts" :key="index">
        <a @click="$router.push({ name: routeName.post, params: { category: item.category, title: item.path }})" class="title-class">
          {{ item.title }}
        </a>
        <span class="dash"></span>
        <span style="margin-right: 5rem; color: #6C757D; font-size: .8rem;">{{ item.date.toDateString() }}</span>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  name: 'tags',
  data () {
    return {
      tag: undefined,
      posts: []
    }
  },
  created () {
    this.tag = this.$route.params.tag
    this.posts = this.$store.state.md.tag[this.tag]
    console.log(this.posts)
  }
}
</script>
<style scoped>
.tag-list{
  list-style-type:none;
  display: flex !important;
  padding: 1rem !important;
}
.tag-list::before{
  background: #999;
  width: 5px;
  height: 5px;
  border-radius: 50%;
  display: block;
  content: "";
  position: relative;
  top: .5rem;
  margin-right: 0.5rem;
}
.title-class{
  font-size: 1.1rem;
  color: #2A408E;
  cursor: pointer;
}
.title-class:hover{
  color:#D2603A;
}
.ul-list{
  line-height: 1.1;
}
.dash{
  margin: 0 .5rem .6rem .5rem;
  border-bottom: 2px dotted silver;
  flex-grow: 1 !important;
}
</style>



